import React, { Suspense, FC } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { BrowserRouter as Router } from 'react-router-dom';
import BounceLoading from 'components/BounceLoading';
import Routes from 'routes';

import 'styles/normalize.scss';
import 'styles/common.scss';

import { userRequest } from 'store/user/actions';
import { goToLogin, getUserId } from 'utils/helpers';
import { teacherRequest } from 'store/teacher/actions';
import { regionPending } from 'store/region/actions';
import { userSelector } from 'store/user/selectors';
import { regionSelector } from 'store/region/selectors';
import PolicyAgreementModal from 'components/PolicyAgreementModal';

const App: FC = () => {
  const dispatch = useDispatch();
  const { info: userInfo } = useSelector(userSelector);
  const { info: regionInfo } = useSelector(regionSelector);
  const userId = getUserId();

  if (!userId) {
    goToLogin();
  }

  if (userId && !userInfo) {
    const userIdNumber = parseInt(userId, 10);

    dispatch(userRequest(userIdNumber));
    dispatch(teacherRequest(userIdNumber));
  }

  if (userInfo && !regionInfo) {
    dispatch(regionPending(userInfo.region_code));
  }

  return (
    <div className="App">
      <Router basename="/novakid-teacher">
        <Suspense fallback={<BounceLoading loading />}>
          <Routes />
        </Suspense>
      </Router>

      <PolicyAgreementModal
        userInfo={userInfo}
        regionInfo={regionInfo}
      />
    </div>
  );
};

export default App;
