// teacher action creators

import {
  TeacherType,
} from 'types/common';

import {
  GetTeacherRequestActionType,
  GetTeacherSuccessActionType,
} from 'types/teacherActionTypes';

import {
  GET_TEACHER_SUCCESS,
  GET_TEACHER_REQUEST,
} from './actionTypes';

export const teacherRequest = (
  teacherId: number,
): GetTeacherRequestActionType => ({
  type: GET_TEACHER_REQUEST,
  payload: teacherId,
});

export const teacherSuccess = (
  teacherInfo: TeacherType,
): GetTeacherSuccessActionType => ({
  type: GET_TEACHER_SUCCESS,
  payload: teacherInfo,
});
