import {
  call,
  takeEvery,
  put,
} from 'typed-redux-saga';

import {
  getAuthToken,
} from 'utils/request';

import { setToken } from 'utils/api';
import paths from 'routes/paths';

import { LoginRequestActionType } from 'types/authActionTypes';
import { loginSuccess, loginFail } from 'store/auth/actions';
import { userRequest } from 'store/user/actions';

import { USER_ID_KEY } from 'constants/common';
import {
  LOGIN_REQUEST,
} from './actionTypes';

function* login({ payload }: LoginRequestActionType) {
  try {
    const { data } = yield* call(getAuthToken, payload.username, payload.password);

    setToken(data.value);

    localStorage.setItem(USER_ID_KEY, data.user_id.toString());

    yield* put(loginSuccess(data));
    yield* call(userRequest, data.user_id);

    window.location.replace(paths.dashboard);
  } catch (e) {
    if (e.response?.data) { // eslint-disable-line
      yield* put(loginFail(e.response.data)); // eslint-disable-line
    }
  }
}

function* loginSaga() {
  yield* takeEvery(LOGIN_REQUEST, login);
}

export default [
  loginSaga(),
];
