import {
  RegionType,
} from 'types/common';

import {
  RegionSuccessAction,
  RegionPendingAction,
} from 'types/regionActionTypes';

import {
  GET_REGION_SUCCESS,
  GET_REGION_PENDING,
} from './actionTypes';

export const regionPending = (regionCode: string): RegionPendingAction => ({
  type: GET_REGION_PENDING,
  payload: { regionCode },
});

export const regionSuccess = (
  regionInfo: RegionType,
): RegionSuccessAction => ({
  type: GET_REGION_SUCCESS,
  payload: { regionInfo },
});
